// 
const BASE_URL = 'http://localhost:8091'
export const  myRequest = (options)=>{
	return new Promise((resolve,reject)=>{
		uni.request({
			url: BASE_URL+options.url,
			method: options.method || 'GET' || 'POST',
			data: options.data || {},
			success: (res) => {
				if(res.data.code !==20000) {
					return uni.showToast({
						title: ''
					})
				}
				resolve(res)
			},
			fail: (err) => {
				uni.showToast({
					title: ''
		})
		reject(err)
		},
		})
	})
}

